<template>
    <div class="container">
        <div class="search">
            <ul>
                <li> <button @click="showMovie" :class="{active :isMovieVisible}">電影</button></li>
                <li> <button @click="showTv" :class="{active :isTvVisible}">戲劇</button> </li>
            </ul>
            <SearchMovie v-show="isMovieVisible"></SearchMovie>
            <SearchDrama v-show="isTvVisible"></SearchDrama>
        </div>
    </div>
</template>

<script>
import SearchMovie from './Search/SearchMovie'
import SearchDrama from './Search/SearchDrama'
export default {
    name: 'Search',
    props: [],
    components: { SearchMovie, SearchDrama },
    data() {
        return {
            isTvVisible: false,
            isMovieVisible: true,
        }
    },
    methods: {
        showMovie() {
            this.isMovieVisible = true;
            this.isTvVisible = false;
        },
        showTv() {
            this.isMovieVisible = false;
            this.isTvVisible = true;
        },
    },
}
</script>

<style lang="scss" scoped>
.search {
    height: 100%;
}

h1 {
    color: white;
}

* {
    list-style: none;
    text-decoration: none;
    box-sizing: border-box;
    padding: 0;
}


ul {
    position: fixed;
    width: 100px;
    padding: 10px;
    left: 3em;

    li {
        padding: 10px 0;

        button {
            padding: 10px 20px;
            background-color: rgba(216, 216, 216, 0);
            font-weight: 700;
            border-radius: 20px;
            border: 1px solid white;
            color: rgb(255, 255, 255);

            &:hover {
                background-color: rgb(255, 255, 255);
                color: black;
            }
        }

    }
}
.active{
    background-color: rgb(255, 255, 255);
                color: black;
}
</style>