<template>
    <div class="movie container">
        <ul>
            <li v-for="movie in genreList" :key="movie.id">
                <router-link :to="{
                    name: 'genreMovie',
                    params: {
                        id: movie.id,
                        name: movie.name
                    }}" @click.native="send">{{movie.name}}</router-link>
            </li>
        </ul>
        <router-view :genreMovie="genreMovie"></router-view>
        <template v-if="$route.name !== 'genreMovie'">
            <ClassMovie></ClassMovie>
            <MarvelMovie></MarvelMovie>
            <HarryPotterMovie></HarryPotterMovie>
            <JapanMovie></JapanMovie>
            <ThrillerMovie></ThrillerMovie>
            <FamilyMovie></FamilyMovie>
            <RomanceMovie></RomanceMovie>
        </template>
    </div>
</template>

<script>
import axios from 'axios'
import ClassMovie from './MovieList/ClassMovie.vue'
import ThrillerMovie from './MovieList/ThrillerMovie.vue'
import FamilyMovie from './MovieList/FamilyMovie.vue'
import MarvelMovie from './MovieList/MarvelMovie.vue'
import RomanceMovie from './MovieList/RomanceMovie.vue'
import JapanMovie from './MovieList/JapanMovie.vue'
import HarryPotterMovie from './MovieList/HarryPotterMovie.vue'

export default {
    name: 'Movie',
    components: { ClassMovie, ThrillerMovie, FamilyMovie ,MarvelMovie,RomanceMovie,HarryPotterMovie,JapanMovie},
    data() {
        return {
            zhTWArray: [
                '動作',
                '冒險',
                '動畫',
                '喜劇',
                '犯罪',
                '紀錄',
                '劇情',
                '家庭',
                '奇幻',
                '歷史',
                '恐怖',
                '音樂',
                '懸疑',
                '愛情',
                '科幻',
                '電視電影',
                '驚悚',
                '戰爭',
                '西部',
            ],
            genreList: [], // 分類標題
            genreMovie: [], // 電影
        }
    },
    mounted() { //獲取電影分類
        const apiKey = process.env.VUE_APP_API_KEY;
        const url = `https://api.themoviedb.org/3/genre/movie/list?api_key=${apiKey}&language=zh-TW`
        axios.get(url)
            .then(response => {
                this.genreList = response.data.genres.forEach((movie,index)=>{
                    movie.name = this.zhTWArray[index]
                })
                this.genreList = response.data.genres
            })
            .catch(error => {
                console.log(error);
            })
    },
    methods: {
        send() { // 傳到子組件GenreMovie
            const apiKey = process.env.VUE_APP_API_KEY;
            const genreId = this.$route.params.id;
            const url = `https://api.themoviedb.org/3/discover/movie?api_key=${apiKey}&include_adult=false&include_video=false&language=zh-TW&page=1&sort_by=popularity.desc&with_genres=${genreId}`;
            axios.get(url)
                .then(response => {
                    this.genreMovie = response.data.results.filter((movie)=>{
                        return movie.poster_path !== null
                    })
                    this.$store.dispatch('genreMovie',this.genreMovie)
                })
                .catch(error => {
                    console.log(error);
                })
        },
        
    }
}
</script>

<style scoped>
.movie {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

a {
    text-decoration: none;
    color: white;
    font-weight: 700;
}

ul {
    list-style: none;
    display: flex;
}

li {
    padding: 12px;
}
</style>