<template>
    <div class="searchHeader">
        <div class="detail-item" v-for=" movie in detailDrama" :key="movie.id"
            :style="{ 'background-image': 'url(' + getMoviePosterUrl(movie.backdrop_path) + ')', }">
            <img class="poster_path" :src="getMoviePosterUrl(movie.poster_path)">
            <div class="title-container">
                <h1>{{ movie.name }} </h1>
                <div class="facts">
                    <p class="release_date">開播日期：{{ movie.first_air_date }} </p>
                    <div class="genre">
                        <span>類型：</span>
                        <p v-for="genres in dramaGenres">
                            {{ genres.name }},
                        </p>
                    </div>
                </div>
            </div>
            <!-- 劇情 -->
            <p class="overview">{{ movie.overview }}</p>
            <!-- 平均評分 -->
            <div class="vote_average">
                <div class="circle" :style="{ '--percent': movie.vote_average }">
                    <div class="circle-inside">
                        {{ movie.vote_average.toFixed(1) }}
                    </div>
                </div>
                <p>平均評分</p>
            </div>

            <a :href="videoUrl" class="videoUrl" target="_blank">
                <i class="bi bi-play-fill">
                </i>預告片
            </a>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    name: 'DetailDramaHeader',
    data() {
        return {
            baseUrl: 'https://image.tmdb.org/t/p/',
            detailDrama: this.$store.state.detailDrama,
            page: 1,
            videoUrl: '',
            zhTWArray: [
                { id: 10759, name: '冒險' },
                { id: 16, name: '動畫' },
                { id: 35, name: '喜劇' },
                { id: 80, name: '犯罪' },
                { id: 99, name: '紀錄' },
                { id: 18, name: '劇情' },
                { id: 10751, name: '家庭' },
                { id: 10762, name: '兒童' },
                { id: 9648, name: '懸疑' },
                { id: 10763, name: '新聞' },
                { id: 10764, name: '真人秀' },
                { id: 10765, name: 'Sci-Fi & Fantasy' },
                { id: 10766, name: '肥皂劇' },
                { id: 10767, name: '脫口秀' },
                { id: 10768, name: 'War & Politics' },
                { id: 37, name: '西部' },
            ],
            dramaGenres: []
        }
    },
    created() {
        const storedDetailDrama = localStorage.getItem('detailDrama');
        if (storedDetailDrama) {
            this.detailDrama = JSON.parse(storedDetailDrama);
        }
    },
    mounted() {
        this.getMovieVideo()
        this.getDramaGenres()
    },
    methods: {
        getMoviePosterUrl(posterPath) {
            return this.baseUrl + 'original' + posterPath;
        },
        getMovieVideo() {
            const apiKey = process.env.VUE_APP_API_KEY;
            const movieId = this.detailDrama[0].id
            const zhUrl = `https://api.themoviedb.org/3/tv/${movieId}/videos?api_key=${apiKey}&language=zh-TW`
            const enUrl = `https://api.themoviedb.org/3/tv/${movieId}/videos?api_key=${apiKey}&language=en-US`
            axios.get(zhUrl)
                .then(response => {
                    // 先請求中文預告片
                    const zhTrailers = response.data.results
                    if (zhTrailers.length > 0) {
                        this.videoUrl = 'https://www.youtube.com/watch?v=' + zhTrailers[0].key
                    } else {
                        // 如果沒有中文預告片，就請求英文預告片
                        axios.get(enUrl)
                            .then(response => {
                                const enTrailers = response.data.results
                                this.videoUrl = 'https://www.youtube.com/watch?v=' + enTrailers[0].key
                            })
                            .catch(error => {
                                console.error(error);
                            })
                    }
                })
                .catch(error => {
                    console.error(error);
                })
        },

        getDramaGenres() {
            const apiKey = process.env.VUE_APP_API_KEY;
            const dramaId = this.detailDrama[0].id
            const apiUrl = `https://api.themoviedb.org/3/tv/${dramaId}?api_key=${apiKey}&language=zh-TW&region=TW`
            axios.get(apiUrl)
                .then(response => {
                    // 將回應中的每個電影類型與 zhTWArray 中的匹配
                    const matchedGenres = response.data.genres.map(apiGenre => {
                        const matchedGenre = this.zhTWArray.find(zhTWGenre => zhTWGenre.id === apiGenre.id);
                        return matchedGenre || apiGenre;
                    });

                    // 將匹配後的數據放入 movieGenres
                    this.dramaGenres = matchedGenres;
                })
                .catch(error => {
                    console.log(error);
                })
        },



    }
}
</script>

<style lang="scss" scoped>
* {
    color: white;
    list-style: none;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.detail-item {
    width: 100%;
    height: 60vh;
    position: relative;
    background-repeat: no-repeat;
    background-position: right center;
    background-size: contain;
    display: flex;
    justify-content: center;

    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        // background-image: linear-gradient(to right,rgb(0, 0, 0) 0%, #000000 60%, #1b76de00 80%,rgb(0, 0, 0) 100%);
        background-image: linear-gradient(-270deg, black 0%, #2b5876 65%, #4e437600 85%, black 100%);
        // box-shadow: 0px 0px 30px 5px rgb(0, 0, 0);
    }

    .poster_path {
        position: absolute;
        top: 10%;
        left: 75px;
        width: 15%;
        border-radius: 5px;
    }

    .title-container {
        display: flex;
        justify-content: center;

        h1 {
            color: white;
            position: absolute;
            left: 22%;
            top: 8%;
        }

        .facts {
            display: flex;
            position: absolute;
            left: 22%;
            top: 20%;

            // 上映日期
            .release_date {
                color: #c2c2c2;
                padding-right: 1.5em;
            }

            // 類型
            .genre {
                display: flex;

                span {
                    color: #c2c2c2;
                }

                p {
                    color: #c2c2c2;
                }
            }
        }
    }

    .overview {
        position: absolute;
        color: white;
        overflow: auto;
        width: 700px;
        height: 150px;
        top: 35%;
        left: 22%;
    }

    // 平均評分
    .vote_average {
        display: flex;
        position: absolute;
        align-items: center;
        bottom: 10%;
        left: 22%;

        .circle {
            position: absolute;
            bottom: 10px;
            z-index: 1;
            --width: 60px;
            --percent: 2;
            --deg: calc(360deg * calc(var(--percent) / 10));
            width: var(--width);
            height: var(--width);
            border-radius: 50%;
            margin: 0px auto;
            background-image: conic-gradient(rgb(255, 136, 0) var(--deg), rgb(104, 66, 22) var(--deg));
            position: relative;

            .circle-inside {
                --bar-width: 12px;
                position: absolute;
                display: flex;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                border-radius: 50%;
                background-color: rgb(10, 26, 35);
                width: calc(var(--width) - var(--bar-width));
                height: calc(var(--width) - var(--bar-width));
                align-items: center;
                justify-content: center;
                color: white;
                font-size: 18px;
                font-weight: 700;
            }
        }

        p {
            color: white;
            margin-left: 10px;
            margin-bottom: 10px;
        }
    }

    .videoUrl {
        z-index: 1;
        text-decoration: none;
        margin-bottom: 70px;
        align-self: flex-end;
    }

    .videoUrl:hover {
        color: rgb(204, 204, 204);
    }
}
</style>