<template>
    <div class="container">
        <h3 class="mt-3">最新上映</h3>
        <div class="nowPlay">
            <div class="swiper-container NowPlay-swiper">
                <div class="swiper-wrapper">
                    <div class="swiper-slide my-swiper-slide" v-for="movie, index in nowPlayList" :key="index">
                        <router-link :to="{
                            name: 'detail',
                            params: {
                                id: movie.id,
                                title: movie.title
                            }
                        }">
                            <img class="poster_path" :src="getMoviePosterUrl(movie.poster_path)" @click="sendDetail(movie)">
                        </router-link>
                        <!-- <a :href="getUrl(movie.id, movie.original_title)">
                            <img class="poster_path" :src="getMoviePosterUrl(movie.poster_path)">
                        </a> -->
                    </div>
                </div>
                <!-- 如果需要分页器 -->
                <div class="swiper-pagination swiper-p-2"></div>
                <!-- 如果需要导航按钮 -->
                <div class="swiper-button-prev NowPlay-prev"><svg xmlns="http://www.w3.org/2000/svg" width="30" height="30"
                        fill="white" class="bi bi-arrow-left-circle-fill" viewBox="0 0 16 16">
                        <path
                            d="M8 0a8 8 0 1 0 0 16A8 8 0 0 0 8 0zm3.5 7.5a.5.5 0 0 1 0 1H5.707l2.147 2.146a.5.5 0 0 1-.708.708l-3-3a.5.5 0 0 1 0-.708l3-3a.5.5 0 1 1 .708.708L5.707 7.5H11.5z" />
                    </svg></div>
                <div class="swiper-button-next NowPlay-next"><svg xmlns="http://www.w3.org/2000/svg" width="30" height="30"
                        fill="white" class="bi bi-arrow-right-circle-fill" viewBox="0 0 16 16">
                        <path
                            d="M8 0a8 8 0 1 1 0 16A8 8 0 0 1 8 0zM4.5 7.5a.5.5 0 0 0 0 1h5.793l-2.147 2.146a.5.5 0 0 0 .708.708l3-3a.5.5 0 0 0 0-.708l-3-3a.5.5 0 1 0-.708.708L10.293 7.5H4.5z" />
                    </svg>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import Swiper from 'swiper'
import 'swiper/css/swiper.min.css'
export default {
    name: 'NowPlay',
    data() {
        return {
            nowPlayList: [],
        }
    },
    updated() {
        this.initSwiper()
    },
    mounted() {
        this.getNowPlay()
    },
    methods: {
        sendDetail(movie) {
            this.$store.dispatch('detailMovie', movie)
        },
        getNowPlay() {
            const apiKey = process.env.VUE_APP_API_KEY;
            axios.get(`https://api.themoviedb.org/3/movie/now_playing?api_key=${apiKey}&language=zh-TW&region=TW`)
            .then(response => {
                    this.nowPlayList = response.data.results
                    // console.log('最新',response);
                })
                .catch(error => {
                    console.error(error);
                })
        },
        getMoviePosterUrl(posterPath) {
            return 'https://image.tmdb.org/t/p/original' + posterPath;
        },
        getUrl(id, movieName) {
            return 'https://www.themoviedb.org/movie/' + id + '-' + movieName
        },
        initSwiper() {
            new Swiper('.NowPlay-swiper', {
                spaceBetween: 30,
                slidesPerGroup: 5, // 一次滑动5张
                breakpoints: {
                    576: {
                        slidesPerView: 2,
                        slidesPerGroup: 2,
                    },
                    768: {
                        slidesPerView: 3,
                        slidesPerGroup: 3,
                    },
                    992: {
                        slidesPerView: 4,
                        slidesPerGroup: 4,
                    },
                    1200: {
                        slidesPerView: 4,
                        slidesPerGroup: 4,
                    },
                    1400: {
                        slidesPerView: 5,
                        slidesPerGroup: 5,
                    }
                },
                navigation: {
                    nextEl: '.NowPlay-next',
                    prevEl: '.NowPlay-prev',
                },
            })
        }
    }
}
</script>

<style scoped>
* {
    box-sizing: border-box;
}

.my-swiper-slide {
    width: 235px;
    height: 352px;
}

.swiper-container {
    --swiper-navigation-size: 0;
}

h3 {
    color: white;
}

a {
    color: white;
    text-decoration: none;
}


img {
    width: 100%;
    height: 100%;
    border-radius: 5px;
    object-fit: contain;
}

.NowPlay-prev svg:hover,
.NowPlay-next svg:hover {
    fill: rgb(188, 188, 188);
}</style>