<template>
    <div class="container">
        <h3 class="mt-5">強檔電影</h3>
        <div class="swiper-container popular-swiper">
            <div class="swiper-wrapper popular-swiper-wrapper">

                <div class="swiper-slide popular-swiper-slide" v-for="(movie, index ) in popularMovies" :key="index"
                    @click="sendDetail(movie)"
                    :style="{ 'background-image': 'url(' + getMoviePosterUrl(movie.backdrop_path) + ')', }">
                    <div class="movieTitle-container">
                        <h3 class="movie-title">{{ movie.title }}</h3>
                    </div>
                    <!-- 電影名稱 -->
                    <router-link :to="{
                        name: 'detail',
                        params: {
                            id: movie.id,
                            title: movie.title
                        }
                    }"><!-- 海報 -->
                        <div class="poster-container" @click="sendDetail(movie)">
                            <img class="poster_path" :src="getMoviePosterUrl(movie.poster_path)">
                        </div>
                    </router-link>
                    <!-- 劇情 -->
                    <div class="overview">
                        <p>{{ movie.overview }}</p>
                    </div>
                    <!-- 平均評分 -->
                    <div class="vote_average">
                        <div class="circle" :style="{ '--percent': movie.vote_average }">
                            <div class="circle-inside">
                                {{ movie.vote_average.toFixed(1) }}
                            </div>
                        </div>
                        <p>平均評分</p>
                    </div>

                </div>

            </div>
            <!-- 如果需要分页器 -->
            <div class="swiper-pagination popular-p"></div>

            <!-- 如果需要导航按钮 -->
            <div class="swiper-button-prev popular-prev"></div>
            <div class="swiper-button-next popular-next"></div>
        </div>


    </div>
</template>

<script>
import axios from 'axios'
import Swiper from 'swiper'

import 'swiper/css/swiper.min.css'
export default {
    name: 'Carousel',
    data() {
        return {
            popularMovies: [],
            baseUrl: 'https://image.tmdb.org/t/p/',
        }
    },
    updated() {
        this.carousel()
    },
    mounted() {
        this.getpopularMovies();
    },
    methods: {
        sendDetail(movie) {
            this.$store.dispatch('detailMovie', movie)
        },
        carousel() {
            this.$nextTick(() => {
                new Swiper('.popular-swiper', {

                    pagination: {
                        el: '.popular-p',
                    },
                    navigation: {
                        prevEl: '.popular-prev',
                        nextEl: '.popular-next',
                    },
                })
            })
        },
        getpopularMovies() {
            const apiKey = process.env.VUE_APP_API_KEY;
            axios.get(`https://api.themoviedb.org/3/movie/popular?api_key=${apiKey}&language=zh-TW&region=TW`)
                .then(response => {
                    this.popularMovies = response.data.results
                    this.popularMovies = response.data.results.filter((movie) => {
                        return movie.overview && movie.overview.trim() !== ''
                    })
                })
                .catch(error => {
                    console.error(error);
                })
        },
        getMoviePosterUrl(posterPath) {
            return this.baseUrl + 'original' + posterPath;
        },
    },
}

</script>

<style lang="scss" scoped>
* {
    color: white;
}

.popular-swiper {
    width: 100%;
    height: 400px;

    .popular-swiper-wrapper {
        width: 100%;
        height: 100%;
    }

    .popular-swiper-slide {
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
        position: relative;
        filter: opacity(100%);

        &::after {
            content: '';
            background-color: rgba(0, 0, 0, 0.5);
            position: absolute;
            left: 0;
            top: 0;
            bottom: 0;
            right: 0;
            z-index: -1;
        }

        .movieTitle-container {
            position: absolute;
            bottom: 78%;
            left: 25%;
            z-index: 1;
            width: 100%;
        }

        .overview {
            position: absolute;
            top: 25%;
            left: 25%;
            width: 60%;
            height: 30%;
            color: white;
            overflow: auto;

        }

        .poster-container {
            position: absolute;
            top: 50px;
            left: 75px;
            width: 15%;
            z-index: 1;
            display: flex;
            align-items: center;
            justify-content: center;

            img {
                width: 100%;
                border-radius: 5px;

            }
        }

        /* 平均評分 */
        .vote_average {
            display: flex;
            position: absolute;
            align-items: center;
            bottom: 13%;
            left: 25%;

            .circle {
                position: absolute;
                bottom: 10px;
                z-index: 1;
                --width: 60px;
                --percent: 2;
                --deg: calc(360deg * calc(var(--percent) / 10));
                width: var(--width);
                height: var(--width);
                border-radius: 50%;
                margin: 0px auto;
                background-image: conic-gradient(rgb(255, 136, 0) var(--deg), rgb(104, 66, 22) var(--deg));
                position: relative;

                .circle-inside {
                    --bar-width: 12px;
                    position: absolute;
                    display: flex;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -50%);
                    border-radius: 50%;
                    background-color: rgb(10, 26, 35);
                    width: calc(var(--width) - var(--bar-width));
                    height: calc(var(--width) - var(--bar-width));
                    align-items: center;
                    justify-content: center;
                    color: white;
                    font-size: 18px;
                    font-weight: 700;
                }
            }

            p {
                color: white;
                margin-left: 10px;
            }
        }
    }
}


@media screen and (max-width:992px) {
    .poster-container {
        img {
            position: absolute;
            top: 10px;
            right: 20px;
            width: 15%;
            z-index: 1;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
}

@media screen and (max-width:576px) {
    .movieTitle-container {
        margin: auto;
    }

    .poster-container {
        img {
            position: absolute;
            top: 10px;
            right: 50px;
            width: 15%;
            z-index: 1;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    .movieTitle-container {
        
        position: absolute;
        bottom: 78%;
        left: 25%;
        z-index: 1;
        width: 100%;
        h3 {
            width: 250px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }
}
</style>