<template>
    <div class="drama container">
        <ul>
            <li v-for="drama in genreList" :key="drama.id">
                <router-link :to="{
                    name: 'genreDrama',
                    params: {
                        id: drama.id,
                        name: drama.name
                    }
                }" @click.native="send">{{ drama.name }}</router-link>
            </li>
        </ul>
        <router-view :genreDrama="genreDrama"></router-view>
        <template v-if="$route.name !== 'genreDrama'">
            <JapanAnime></JapanAnime>
            <CrimeDrama></CrimeDrama>
            <ComedyDrama></ComedyDrama>
        </template>

    </div>
</template>

<script>
import axios from 'axios'
import JapanAnime from './DramaList/JapanAnime'
import CrimeDrama from './DramaList/CrimeDrama'
import ComedyDrama from './DramaList/ComedyDrama'
export default {
    name: 'Drama',
    components: { JapanAnime ,CrimeDrama,ComedyDrama},
    data() {
        return {
            zhTWArray: [
                '冒險',
                '動畫',
                '喜劇',
                '犯罪',
                '紀錄',
                '劇情',
                '家庭',
                '兒童',
                '懸疑',
                '新聞',
                '真人秀',
                'Sci-Fi & Fantasy',
                '肥皂劇',
                '脫口秀',
                'War & Politics',
                '西部'
            ],
            genreList: [], // 分類標題
            genreDrama: [] // 戲劇
        }
    },
    mounted() {
        this.getGenreDrama()
    },
    methods: {
        getGenreDrama() {
            const apiKey = process.env.VUE_APP_API_KEY;
            const url = `https://api.themoviedb.org/3/genre/tv/list?api_key=${apiKey}&language=zh-TW`
            axios.get(url)
                .then(response => {
                    this.genreList = response.data.genres.forEach((movie, index) => {
                        movie.name = this.zhTWArray[index]
                    })
                    this.genreList = response.data.genres;
                })
                .catch(error => {
                    console.log(error);
                })
        },
        send() { // 傳到子組件GenreDrama
            const apiKey = process.env.VUE_APP_API_KEY;
            const genreId = this.$route.params.id;
            const url = `https://api.themoviedb.org/3/discover/tv?api_key=${apiKey}&include_adult=false&include_video=false&language=zh-TW&page=1&sort_by=popularity.desc&with_genres=${genreId}`;
            axios.get(url)
                .then(response => {
                    this.genreDrama = response.data.results.filter((drama) => {
                        return drama.poster_path !== null
                    })
                    this.$store.dispatch('genreDrama', this.genreDrama)
                })
                .catch(error => {
                    console.log(error);
                })
        },
    }

}
</script>

<style scoped>
.drama {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

a {
    text-decoration: none;
    color: white;
    font-weight: 700;
}

ul {
    list-style: none;
    display: flex;
}

li {
    padding: 10px;
}
</style>