import VueRouter from "vue-router";
import Movie from '../page/Movie'
import Home from '../page/Home'
import GenreMovie from '../page/GenreMovie'
import Drama from '../page/Drama'
import GenreDrama from '../page/GenreDrama'
import Search from '../page/Search'
import Detail from '../page/Detail'
import DetailDrama from '../page/DetailDrama'
const router = new VueRouter({
    routes: [
        {
            path: '/home',
            component: Home,
            meta: { title: '首頁' }
        },
        {
            path: '/movie',
            component: Movie,
            meta: { title: '電影清單' },
            children: [
                {
                    name: 'genreMovie',
                    path: 'genreMovie/:id/:name',
                    component: GenreMovie,
                }
            ]
        },
        {
            path: '/drama',
            component: Drama,
            meta: { title: '戲劇清單' },
            children: [
                {
                    name: 'genreDrama',
                    path: 'genreDrama/:id/:name',
                    component: GenreDrama,
                }
            ]
        },
        {
            name: 'search',
            path: '/search',
            component: Search,
            props: true,
        },
        {
            name: 'detail', //電影細節
            path: '/detail-movie/:id',
            component: Detail,
        },
        {
            name: 'detailDrama', //戲劇細節
            path: '/detail-drama/:id',
            component: DetailDrama,
        },
        {
            path: '*',
            redirect: '/home'
        },
    ]
})

router.beforeEach((to, from, next) => {
    if (to.path === '/search') {
        const queryTitle = to.query.title;
        to.meta.title = `搜尋結果 - ${queryTitle}`;
    }
    if (to.name === 'genreDrama') {
        const genreDramaTitle = to.params.name
        to.meta.title = `戲劇 - ${genreDramaTitle}`;
    }
    if (to.name === 'genreMovie') {
        const genreMovieTitle = to.params.name
        to.meta.title = `電影 - ${genreMovieTitle}`;
    }
    if (to.name === 'detail') {
        const detailMovieTitle = to.params.title
        to.meta.title = detailMovieTitle
    }
    if (to.name === 'detailDrama') {
        const detailDramalTitle = to.params.title
        to.meta.title = detailDramalTitle
    }
    next();
})
router.afterEach((to, from) => {
    document.title = to.meta.title || '首頁'
})

export default router