<template>
    <div class="header">
        <nav class="navbar navbar-expand-lg navbar-light container">
            <div class="container justify-content-center">
                <!-- logo -->
                <router-link class="logo" to="/home">
                    <img src="../../public/img/3.png" alt="">
                </router-link>
                <!-- navbar-toggler -->
                <button class="navbar-toggler" type="button" data-bs-toggle="collapse"
                    data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false"
                    aria-label="Toggle navigation">
                    <span class="navbar-toggler-icon"></span>
                </button>
                <!-- nav -->
                <div class="collapse navbar-collapse  my-navbar-collapse " id="navbarSupportedContent">
                    <ul class="navbar-nav me-auto ms-2 mb-2 mb-lg-0 ">
                        <li class="nav-item ">
                            <router-link class="nav-link" to="/movie" id="navbarDropdown" role="button"
                                aria-expanded="false">電影</router-link>
                        </li>
                        <li class="nav-item ">
                            <router-link class="nav-link" to="/drama" id="navbarDropdown" role="button"
                                aria-expanded="false">戲劇</router-link>
                        </li>
                        <!-- 搜尋欄 -->
                        <li class="nav-item ms-lg-5 ">
                            <div class="d-flex input-container ">

                                <input v-model="ipt" @keyup.enter="search" class="form-control me-2 search-input"
                                    placeholder="搜尋" aria-label="Search">

                                <button class="btn" @click="search" type="submit">
                                    <i class="bi bi-search"></i>
                                </button>

                            </div>
                        </li>
                    </ul>
                </div>

            </div>
        </nav>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    name: 'Header',
    data() {
        return {
            ipt: '',
        }
    },
    methods: {
        async search() {
            try {
                // 搜尋不能為空
                if (!this.ipt.trim()) {
                    return;
                }
                const apiKey = process.env.VUE_APP_API_KEY;
                // 在搜索前清空数组
                this.$store.commit('CLEAR_SEARCH_RESULTS');
                // 搜尋電影
                const movieResponse = await axios.get(`https://api.themoviedb.org/3/search/movie?api_key=${apiKey}&language=zh-TW&query=${this.ipt}`);
                this.$store.dispatch('searchMovies', movieResponse.data.results);

                //搜尋戲劇
                const dramaResponse = await axios.get(`https://api.themoviedb.org/3/search/tv?api_key=${apiKey}&language=zh-TW&query=${this.ipt}`);
                this.$store.dispatch('searchDramas', dramaResponse.data.results);

                // 更新 ipt 的值
                this.$store.dispatch('updateIpt', this.ipt);

                await this.$router.push({
                    name: 'search',
                    query: {
                        title: this.$store.state.ipt
                    },
                });

            } catch (error) {
                console.log('搜尋錯誤', error);
            }
        },

    },
}
</script>

<style lang="scss" scoped>
.header {
    position: fixed;
    z-index: 3;
    width: 100%;
    top: 0;
    background-color: rgb(230, 230, 230);
}

li {
    font-weight: 700;
}

.logo {
    width: 250px;
    height: 50px;

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}



.input-container {
    position: relative;
    width: 500px;
    margin: auto; 
    input {
        width: 100%;
        border-radius: 20px;

        &:focus {
            box-shadow: 0 0 5px rgba(0, 77, 128, 0.5);
        }
    }

    button {
        background: none;
        border: none;
        position: absolute;
        right: 10px;

        i {
            color: black;
        }

        &:hover {
            background: none;
            border: none;
            color: black;
        }

        &:focus {
            box-shadow: none;
        }
    }
}
@media screen and (max-width:992px){
    ul{
        display: flex;
        flex-direction: row;
        justify-content: center;
        li{
            padding: 10px;
        }
    }
    .input-container{
        width: auto;
        input{
            width: 100%;
        }
    }
}
</style>