<template>
    <div class="search">
        <div class="iptNull" v-if="$store.state.searchMovie.length === 0">
            <h1>查無相關資料</h1>
        </div>
        <div v-else class="movie container">
            <ul>
                <li class="mt-3" v-for="movie in $store.state.searchMovie">
                    <router-link :to="{
                        name: 'detail',
                        params: {
                            id: movie.id,
                            title: movie.title
                        }
                    }">
                        <img class="poster_path" :src="getMoviePosterUrl(movie.poster_path)" @click="sendDetail(movie)">
                        {{ movie.title }}
                    </router-link>
                </li>
            </ul>

            <div class="d-flex justify-content-center mt-5">
                <button class="load-btn" @click="loadMoreMovies">載入更多</button>
            </div>
        </div>

    </div>
</template>

<script>
import axios from 'axios'

export default {
    props: [],
    data() {
        return {
            currentPage: 2,
            baseUrl: 'https://image.tmdb.org/t/p/',
            ipt: ''
        }
    },
    created() {
        this.$bus.$on('search', (ipt) => {
            this.ipt = ipt;
        });
    },
    beforeRouteUpdate(to, from, next) {
        this.currentPage = 2;
        next();
    },
    methods: {
        sendDetail(movie) {
            this.$store.dispatch('detailMovie', movie)
        },
        getMoviePosterUrl(posterPath) {
            return this.baseUrl + 'original' + posterPath;
        },
        loadMoreMovies() {
            const apiKey = process.env.VUE_APP_API_KEY;
            const query = this.ipt ? this.ipt : this.$store.state.ipt;
            const url = `https://api.themoviedb.org/3/search/movie?api_key=${apiKey}&language=zh-TW&query=${query}&page=${this.currentPage}`;
            axios.get(url)
                .then(response => {
                    console.log(this.currentPage);
                    const newMovies = response.data.results;
                    this.$store.commit('ADD_MOVIES_TO_SEARCH_MOVIE', newMovies);
                    this.currentPage++
                })
                .catch(error => {
                    console.error(error);
                });
        }
    }
}
</script>

<style lang="scss" scoped>
* {
    list-style: none;
    text-decoration: none;
    box-sizing: border-box;
    padding: 0;
}

.search {
    height: 100%;
}

h1 {
    color: white;
}

.iptNull {
    text-align: center;
    height: 100vh;

    h1 {
        line-height: 10vh;
    }
}

ul {
    display: flex;
    flex-wrap: wrap;

    li {
        width: 195px;
        margin-left: 20px;
    }

}


a {
    display: block;
    font-weight: 700;
    color: white;
}

a:hover {
    color: orange;

}

img {
    width: 100%;
    height: 274px;
    object-fit: fill;
    border-radius: 5px;
}

.load-btn {
    border-radius: 20px;
    border: 1px solid rgb(255, 255, 255);
    background-color: rgba(255, 0, 0, 0);
    color: white;
    font-weight: 700;
    padding: 10px 50px;
}

.load-btn:hover {
    background-color: rgb(255, 255, 255);
    color: black;
}</style>