<template>
    <div class="detailCast mt-3">
        <div class="swiper-container swiper2">
            <h2>主要演員</h2>
            <div class="swiper-wrapper">
                <div class="swiper-slide mt-3" v-for="movie in CastList">
                    <img :src="getMoviePosterUrl(movie.profile_path)" v-if="movie.profile_path !== null">
                    <img src="../../../public/img/person.png" v-else>
                    <p class="p-2">{{ movie.name }}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import Swiper from 'swiper'
import 'swiper/css/swiper.min.css'

export default {
    name: 'DetailCast',
    data() {
        return {
            detailMovie: this.$store.state.detailMovie,
            CastList: []
        }
    },
    updated() {
        new Swiper('.swiper2', {
            spaceBetween: 30,
            slidesPerGroup: 5, // 一次滑动5张
            loop: true, // 循环模式选项
            breakpoints: {
                576: {
                    slidesPerView: 2,
                    slidesPerGroup: 1,
                },
                768: {
                    slidesPerView: 4,
                    slidesPerGroup: 4,
                },
                992: {
                    slidesPerView: 5,
                    slidesPerGroup: 5,
                },
                1200: {
                    slidesPerView: 6,
                    slidesPerGroup: 6,
                },
                1400: {
                    slidesPerView: 7,
                    slidesPerGroup: 7,
                }
            },
            navigation: {
                nextEl: '.next-2',
                prevEl: '.prev-2',
            },
        })
    },
    created() {
        const storedDetailMovie = localStorage.getItem('detailMovie');
        if (storedDetailMovie) {
            this.detailMovie = JSON.parse(storedDetailMovie);
        }
    },
    mounted() {
        this.getCredit()
    },
    created() {
        const storedDetailMovie = localStorage.getItem('detailMovie');
        if (storedDetailMovie) {
            this.detailMovie = JSON.parse(storedDetailMovie);
        }
    },
    methods: {
        getMoviePosterUrl(posterPath) {
            return 'https://image.tmdb.org/t/p/original/' + posterPath;
        },
        getCredit() {
            const apiKey = process.env.VUE_APP_API_KEY;
            const movieId = this.detailMovie[0].id
            axios.get(`https://api.themoviedb.org/3/movie/${movieId}/credits?api_key=${apiKey}&language=zh-TW&region=TW`)
                .then(response => {
                    this.CastList = response.data.cast
                })
                .catch(error => {
                    console.log(error);
                })
        },
    },

}

</script>


<style lang="scss" scoped>
* {
    box-sizing: border-box;
    margin: 0;
}

.detailCast {
    padding: 10px;
}

.swiper-container {
    width: 100%;
    height: auto;
    padding: 10px;

    .swiper-slide {
        border-radius: 5px;
        box-shadow: 2px 2px 60px 1px black;

        img {
            width: 100%;
            height: 100%;
            border-radius: 5px;
        }

        p {
            height: 60px;
        }
    }
}
</style>