<template>
    <div class="container">
        <h3 class="mt-3">漫威宇宙</h3>
        <div class="nowPlay">
            <div class="swiper-container MarvelMovie-swiper">
                <div class="swiper-wrapper">
                    <div class="swiper-slide my-swiper-slide" v-for="movie, index in MarvelMovie" :key="index">
                        <router-link :to="{
                            name: 'detail',
                            params: {
                                id: movie.id,
                                title: movie.title
                            }
                        }">
                            <img class="poster_path" :src="getMoviePosterUrl(movie.poster_path)" @click="sendDetail(movie)">
                        </router-link>
                        <!-- <a :href="getUrl(movie.id, movie.original_title)">
                            <img class="poster_path" :src="getMoviePosterUrl(movie.poster_path)">
                        </a> -->
                    </div>
                </div>
                <!-- 如果需要分页器 -->
                <div class="swiper-pagination swiper-p-2"></div>
                <!-- 如果需要导航按钮 -->
                <div class="swiper-button-prev MarvelMovie-prev"><svg xmlns="http://www.w3.org/2000/svg" width="30"
                        height="30" fill="white" class="bi bi-arrow-left-circle-fill" viewBox="0 0 16 16">
                        <path
                            d="M8 0a8 8 0 1 0 0 16A8 8 0 0 0 8 0zm3.5 7.5a.5.5 0 0 1 0 1H5.707l2.147 2.146a.5.5 0 0 1-.708.708l-3-3a.5.5 0 0 1 0-.708l3-3a.5.5 0 1 1 .708.708L5.707 7.5H11.5z" />
                    </svg></div>
                <div class="swiper-button-next MarvelMovie-next"><svg xmlns="http://www.w3.org/2000/svg" width="30"
                        height="30" fill="white" class="bi bi-arrow-right-circle-fill" viewBox="0 0 16 16">
                        <path
                            d="M8 0a8 8 0 1 1 0 16A8 8 0 0 1 8 0zM4.5 7.5a.5.5 0 0 0 0 1h5.793l-2.147 2.146a.5.5 0 0 0 .708.708l3-3a.5.5 0 0 0 0-.708l-3-3a.5.5 0 1 0-.708.708L10.293 7.5H4.5z" />
                    </svg>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import Swiper from 'swiper'
import 'swiper/css/swiper.min.css'
export default {
    name: 'MarvelMovie',
    data() {
        return {
            MarvelMovie: [], 
            baseUrl: 'https://image.tmdb.org/t/p/',
        }
    },
    updated() {
        this.initSwiper()
    },
    mounted() {
        this.getMarvelMovie()
    },
    methods: {
        sendDetail(movie) {
            this.$store.dispatch('detailMovie', movie)
        },
        getMarvelMovie() { 
            const apiKey = process.env.VUE_APP_API_KEY;
            const language = 'zh-TW'
            const url = `https://api.themoviedb.org/3/discover/movie?api_key=${apiKey}&language=${language}&with_companies=420`;
            axios.get(url)
                .then(response => {
                    this.MarvelMovie = response.data.results
                })
                .catch(error => {
                    console.log(error);
                })
        },
        getMoviePosterUrl(posterPath) {
            return this.baseUrl + 'original' + posterPath;
        },
        getUrl(id, movieName) {
            return 'https://www.themoviedb.org/movie/' + id + '-' + movieName
        },
        initSwiper() {
            new Swiper('.MarvelMovie-swiper', {
                spaceBetween: 20,
                breakpoints: {
                    576: {
                        slidesPerView: 2,
                        slidesPerGroup: 2,
                    },
                    768: {
                        slidesPerView: 3,
                        slidesPerGroup: 3,
                    },
                    992: {
                        slidesPerView: 4,
                        slidesPerGroup: 4,
                    },
                    1200: {
                        slidesPerView: 5,
                        slidesPerGroup: 5,
                    },
                    1400: {
                        slidesPerView: 6,
                        slidesPerGroup: 6,
                    },
                },
                navigation: {
                    nextEl: '.MarvelMovie-next',
                    prevEl: '.MarvelMovie-prev',
                },
            });
        },
    }
}
</script>


<style scoped>
* {
    box-sizing: border-box;
}

h3 {
    color: white;
}

.swiper-container {
    width: 100%;
    --swiper-navigation-size: 0;
    /* 设置按钮大小 */
}

a {
    color: white;
    text-decoration: none;
}

img {
    width: 100%;
    height: 274px;
    border-radius: 5px;
}

.MarvelMovie-prev svg:hover,
.MarvelMovie-next svg:hover {
    fill: rgb(188, 188, 188);
}
</style>