<template>
    <div class="genreMovie container">
        <ul>
            <li class="mt-3" v-for=" movie in $store.state.genreMovie" :key="movie.id">
                <router-link :to="{
                    name: 'detail',
                    params: {
                        id: movie.id,
                    }
                }">
                    <img class="poster_path" :src="getMoviePosterUrl(movie.poster_path)" @click="sendDetail(movie)">
                    <p class="mt-1 movieName">{{ movie.title }}</p>
                </router-link>
            </li>
        </ul>
        <div class="d-flex justify-content-center">
            <button class="load-btn" @click="loadMoreMovies">載入更多</button>
        </div>
        <hr>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    name: 'GenreMovie',
    data() {
        return {
            baseUrl: 'https://image.tmdb.org/t/p/',
            page: 1
        }
    },
    methods: {
        sendDetail(movie) {
            this.$store.dispatch('detailMovie',movie)
        },
        getMoviePosterUrl(posterPath) {
            return this.baseUrl + 'original' + posterPath;
        },
        getUrl(id, movieName) {
            return 'https://www.themoviedb.org/movie/' + id + '-' + movieName
        },
        loadMoreMovies() {
            this.page++;
            const genreId = this.$route.params.id;
            const apiKey = process.env.VUE_APP_API_KEY;
            const url = `https://api.themoviedb.org/3/discover/movie?api_key=${apiKey}&include_adult=false&include_video=false&language=zh-TW&page=${this.page}&sort_by=popularity.desc&with_genres=${genreId}`;
            axios.get(url)
                .then(response => {
                    const newMovies = response.data.results;
                    // 把新加載的戲劇數據到 Vuex store 中的 genreDrama
                    this.$store.commit('ADD_MOVIES_TO_GENRE_MOVIE', newMovies);
                })
                .catch(error => {
                    console.error(error);
                });
        }
    }
}
</script>

<style scoped>
* {
    list-style: none;
    text-decoration: none;
    box-sizing: border-box;
    padding: 0;
}

ul {
    display: flex;
    flex-wrap: wrap;
}

li {
    width: 195px;
    margin-left: 20px;
}

a {
    display: block;
    font-weight: 700;
    color: white;
}

a:hover {
    color: orange;
}

img {
    width: 100%;
    height: 274px;
    object-fit: fill;
    border-radius: 5px;
}

.load-btn {
    border-radius: 20px;
    border: 1px solid rgb(255, 255, 255);
    background-color: rgba(255, 0, 0, 0);
    color: white;
    font-weight: 700;
    padding: 10px 50px;
}

.load-btn:hover {
    background-color: rgb(255, 255, 255);
    color: black;
}

.movieName {
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
</style>