<template>
    <div class="DetailBackDrops">
        <div class="swiper-container swiper-backPath">
            <h2>背景圖片</h2>
            <div class="swiper-wrapper">
                <div class="swiper-slide mt-3" v-for="movie in movieImg">
                    <a :href="getUrl(movie.file_path)" target="_blank">
                        <img :src="getMovieBackDropsUrl(movie.file_path)">
                    </a>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import Swiper from 'swiper'
import 'swiper/css/swiper.min.css'

export default {
    name:'DetailBackDrops',
    data() {
        return {
            baseUrl: 'https://image.tmdb.org/t/p/',
            movieImg: [],
            detailMovie: this.$store.state.detailMovie,
        }
    },
    updated() {
        new Swiper('.swiper-backPath', {
            spaceBetween: 30,
            slidesPerGroup: 5, // 一次滑动5张
            loop: true, // 循环模式选项

            breakpoints: {
                576: {
                    slidesPerView: 1,
                    slidesPerGroup: 2,
                },
                768: {
                    slidesPerView: 3,
                    slidesPerGroup: 3,
                },
                992: {
                    slidesPerView: 4,
                    slidesPerGroup: 4,
                },
                1200: {
                    slidesPerView: 4,
                    slidesPerGroup: 4,
                },
                1400: {
                    slidesPerView: 2,
                    slidesPerGroup: 2,
                }
            },
            navigation: {
                nextEl: '.next-2',
                prevEl: '.prev-2',
            },
        })
    },
    mounted() {
        this.getMovieImg()
    },
    created(){
        const storedDetailMovie = localStorage.getItem('detailMovie');
        if (storedDetailMovie) {
            this.detailMovie = JSON.parse(storedDetailMovie);
        }
    },
    methods: {
        getMovieBackDropsUrl(backdrops) {
            return this.baseUrl + 'original' + backdrops;
        },
        getUrl(file_path) {
            return 'https://www.themoviedb.org/t/p/original/' + file_path
        },
        getMovieImg() {
            const apiKey = process.env.VUE_APP_API_KEY;
            const movieId = this.detailMovie[0].id
            axios.get(`https://api.themoviedb.org/3/movie/${movieId}/images?api_key=${apiKey}`)
                .then(response => {
                    this.movieImg = response.data.backdrops
                })
                .catch(error => {
                    console.error(error);
                })
        }
    }
}
</script>

<style lang="scss" scoped>
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
.DetailBackDrops{
    padding: 10px;
}
.swiper-container {
    width: 100%;
    height: auto;
    padding: 10px;

    .swiper-slide {
        border-radius: 5px;
        box-shadow: 2px 2px 60px 1px black;
        img {
            width: 100%;
            border-radius: 5px;
        }
    }
}
</style>