//该文件用于创建Vuex中最为核心的store
import Vue from 'vue'
//引入Vuex
import Vuex from 'vuex'

const actions = {
    search(context, value) {
        context.commit('SEARCH', value)
    },
    popular(context, value) {
        context.commit('POPULAR', value)
    },
    genreDrama(context, value) {
        context.commit('GENNREDRAMA', value)
    },
    genreMovie(context, value) {
        context.commit('GENNREMOVIE', value)
    },
    detailMovie(context, value) {
        context.commit('DETAILMOVIE', value)
    },
    detailDrama(context, value) {
        context.commit('DETAILDRAMA', value)
    },
    searchMovies(context, value) {
        context.commit('SEARCH_MOVIES', value);
    },
    searchDramas(context, value) {
        context.commit('SEARCH_DRAMAS', value);
    },
    updateIpt(context, value) {
        context.commit('UPDATE_IPT', value)
    }
}

const mutations = {
    SEARCH(state, value) {
        state.searchMovie = value
    },
    CLEAR_SEARCH_MOVIE(state) {
        state.searchMovie = [];
    },
    POPULAR(state, value) {
        state.popularMovies = value
    },
    GENNREDRAMA(state, value) {
        state.genreDrama = value.filter((drama) => {
            return drama.poster_path !== null
        })
    },
    ADD_MOVIES_TO_GENRE_DRAMA(state, value) { //載入更多戲劇數據
        state.genreDrama = state.genreDrama.concat(value).filter((drama) => {
            return drama.poster_path !== null
        })
    },
    GENNREMOVIE(state, value) {
        state.genreMovie = value.filter((movie) => {
            return movie.poster_path !== null
        })
    },
    ADD_MOVIES_TO_GENRE_MOVIE(state, value) { //載入更多電影數據
        state.genreMovie = state.genreMovie.concat(value).filter((movie) => {
            return movie.poster_path !== null
        })
    },
    DETAILMOVIE(state, value) { // 電影細節
        if (state.detailMovie.length === 1) {
            state.detailMovie.shift(); // 刪除第一項
        }
        state.detailMovie.push(value)
        localStorage.setItem('detailMovie', JSON.stringify(state.detailMovie));
    },
    DETAILDRAMA(state, value) { // 戲劇細節
        if (state.detailDrama.length === 1) {
            state.detailDrama.shift(); // 刪除第一項
        }

        state.detailDrama.push(value)
        localStorage.setItem('detailDrama', JSON.stringify(state.detailDrama));
    },
    ADD_MOVIES_TO_SEARCH_MOVIE(state, value) {//載入更多電影搜尋數據
        state.searchMovie = state.searchMovie.concat(value).filter((movie) => {
            return movie.poster_path !== null
        })
    },
    ADD_MOVIES_TO_SEARCH_DRAMA(state, value) {//載入更多戲劇搜尋數據
        state.searchDrama = state.searchDrama.concat(value).filter((movie) => {
            return movie.poster_path !== null
        })
    },
    SEARCH_MOVIES(state, value) { // 搜尋電影
        state.searchMovie = state.searchMovie.concat(value).filter((movie) => {
            return movie.poster_path !== null
        })
    },
    SEARCH_DRAMAS(state, value) { // 搜尋戲劇
        state.searchDrama = state.searchDrama.concat(value).filter((movie) => {
            return movie.poster_path !== null
        })
    },
    UPDATE_IPT(state, value) {
        state.ipt = value
        console.log('ipt', value);
    },
    CLEAR_SEARCH_RESULTS(state) { // 清空搜尋陣列
        state.searchMovie = [];
        state.searchDrama = [];
    },
}

const state = {
    searchMovie: [],
    searchDrama: [],
    popularMovies: [],
    genreDrama: [],
    genreMovie: [],
    detailMovie: [],
    detailDrama: [],
    ipt: ''
}

//应用Vuex插件
Vue.use(Vuex)
export default new Vuex.Store({
    actions,
    mutations,
    state,
})