<template>
    <keep-alive>

        <div class="detailCast mt-5">
            <div class="swiper-container swiper2">
                <h2>主要演員</h2>
                <div class="swiper-wrapper">
                    <div class="swiper-slide" v-for="movie in CastList">
                        <img :src="getMoviePosterUrl(movie.profile_path)" v-if="movie.profile_path !== null">
                        <img src="../../../public/img/person.png" v-else>

                        <p class="p-2">{{ movie.name }}</p>
                    </div>
                </div>
            </div>
        </div>
    </keep-alive>
</template>

<script>
import axios from 'axios'
import Swiper from 'swiper'
import 'swiper/css/swiper.min.css'

export default {
    name: 'DetailCast',
    data() {
        return {
            baseUrl: 'https://image.tmdb.org/t/p/',
            detailDrama: this.$store.state.detailDrama,
            CastList: []
        }
    },
    updated() {
        new Swiper('.swiper2', {
            slidesPerView: 5,
            spaceBetween: 30,
            slidesPerGroup: 5, // 一次滑动5张
            loop: true, // 循环模式选项

            breakpoints: {
                576: {
                    slidesPerView: 2,
                    slidesPerGroup: 2,
                },
                768: {
                    slidesPerView: 3,
                    slidesPerGroup: 3,
                },
                992: {
                    slidesPerView: 4,
                    slidesPerGroup: 4,
                },
                1200: {
                    slidesPerView: 4,
                    slidesPerGroup: 4,
                },
                1400: {
                    slidesPerView: 7,
                    slidesPerGroup: 7,
                }
            },
            navigation: {
                nextEl: '.next-2',
                prevEl: '.prev-2',
            },
        })
    },
    created() {
        const storedDetailDrama = localStorage.getItem('detailDrama');
        if (storedDetailDrama) {
            this.detailDrama = JSON.parse(storedDetailDrama);
        }
    },
    mounted() {
        this.getCredit()
    },

    methods: {
        getMoviePosterUrl(posterPath) {
            return this.baseUrl + 'original' + posterPath;
        },
        getCredit() {
            const apiKey = process.env.VUE_APP_API_KEY;
            const movieId = this.detailDrama[0].id
            axios.get(`https://api.themoviedb.org/3/tv/${movieId}/credits?api_key=${apiKey}&language=zh-TW&region=TW`)
                .then(response => {
                    this.CastList = response.data.cast
                })
                .catch(error => {
                    console.log(error);
                })
        },

    },

}

</script>


<style lang="scss" scoped>
* {
    box-sizing: border-box;
    margin: 0;
}

.detailCast {
    padding: 10px;
}

.swiper-container {
    width: 100%;
    height: auto;
    padding: 10px;

    .swiper-slide {
        border-radius: 5px;
        box-shadow: 2px 2px 60px 1px black;

        img {
            width: 100%;
            height: 100%;
            border-radius: 5px;
        }

        p {
            height: 60px;
        }
    }
}
</style>