<template>
    <div class="DetailSimilar mt-3 ">
        <h2 class="mb-3">推薦戲劇</h2>
        <div class="swiper-container  similar-swiper">
            <div class="swiper-wrapper">

                <div class="swiper-slide similar-swiper-slide " v-for="movie in similarMovie">
                    <router-link :to="{
                        name: 'detail',
                        params: {
                            id: movie.id,
                            title: movie.name
                        }
                    }">
                        <img @click="sendDetail(movie)" :src="getMoviePosterUrl(movie.backdrop_path)">
                        <p class="mt-1">{{ movie.name }}</p>
                    </router-link>
                </div>

            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import Swiper from 'swiper'
import 'swiper/css/swiper.min.css'
export default {
    name: 'DetailDramaSimilar',
    data() {
        return {
            detailDrama: this.$store.state.detailDrama,
            similarMovie: []
        }
    },
    updated() {
        new Swiper('.similar-swiper', {
            spaceBetween: 30,
            direction: 'vertical', // 將 Swiper 設置為垂直方向
            mousewheel: true,      // 啟用滾輪控制
            breakpoints: {
                576: {
                    slidesPerView: 2,
                    slidesPerGroup: 3,
                },
                768: {
                    slidesPerView: 3,
                    slidesPerGroup: 3,
                },
                992: {
                    slidesPerView: 3,
                    slidesPerGroup: 3,
                },
                1200: {
                    slidesPerView: 4,
                    slidesPerGroup: 3,
                },
                1400: {
                    slidesPerView: 4,
                    slidesPerGroup: 3,
                }
            },

        })
    },
    created() {
        const storedDetailDrama = localStorage.getItem('detailDrama');
        if (storedDetailDrama) {
            this.detailDrama = JSON.parse(storedDetailDrama);
        }
    },
    mounted() {
        this.getSimilarMovie()
    },

    methods: {
        sendDetail(movie) {
            this.$store.dispatch('detailDrama', movie)
            this.$router.go(0); 
        },
        getMoviePosterUrl(poster_path) {
            return 'https://image.tmdb.org/t/p/original/' + poster_path;
        },
        getSimilarMovie() {
            const apiKey = process.env.VUE_APP_API_KEY;
            const movieId = this.detailDrama[0].id

            const apiUrl = `https://api.themoviedb.org/3/tv/${movieId}/recommendations?api_key=${apiKey}&language=zh-TW`
            axios.get(apiUrl)
                .then(response => {
                    this.similarMovie = response.data.results.filter((movie) => {
                        return movie.backdrop_path !== null
                    })
                })
                .catch(error => {
                    console.log(error);
                })
        },
    }
}
</script>
<style lang="scss" scoped>
* {
    box-sizing: border-box;
    margin: 0;
}

a {
    text-decoration: none;
    color: white;
}

.DetailSimilar {
    padding: 10px;
}

.similar-swiper {
    width: 100%;
    height: 100vh;
    padding: 10px;
    box-shadow: 2px 2px 60px 1px black;

    .similar-swiper-slide {
        width: 100%;
        height: 100px;
        border-radius: 5px;

        img {
            width: 100%;
            height: 80%;
            object-fit: cover;
            border-radius: 5px;
        }

        p {
            width: 100%;
            height: 20%;
            margin-left: 5px;
        }

    }
}
</style>